export default {
	data() {
		return {
			address:{},
			show:false,
			add:''
		}
	},
	onLoad(option) {
		console.log(option)
		this.address = option
		this.add = option.addAdd + option.addName
		if (Object.keys(option).length === 0) {
		    return this.show = false // 如果为空,返回false
		}
		return this.show = true
	},
	methods: {
		clickNavi(){
			uni.navigateTo({
				url:'./edit'
			})
		},
		clickNavi2(){
			uni.navigateTo({
				url:'./shipping'
			})
		}
	}
}
